import React from "react";
import { connect } from "react-redux";
import Polygon from "./Polygon";
import TodoHistory from "./TodoHistory/TodoHistory";
import { format } from "date-fns";
import _ from "lodash";
import "./Statistics.scss";

interface IProps {
  todos: any[];
}

class Statistics extends React.Component<IProps> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  get finishedTodos() {
    return this.props.todos.filter((t) => t.completed && !t.deleted);
  }

  get dailyTodos() {
    return _.groupBy(this.finishedTodos, (todo) => {
      return format(Date.parse(todo.updated_at), "yyyy-MM-d");
    });
  }

  render() {
    return (
      <div className="Statistics" id="Statistics">
        <ul>
          {/* <li>ç»Ÿè®¡</li>
          <li>ç›®æ ‡</li>
          <li>ç•ªèŒ„å†å²</li> */}
          <li>
            ä»»åŠ¡å†å² ç´¯è®¡å®Œæˆ{this.finishedTodos.length}ä¸ªä»»åŠ¡
            <Polygon
              data={this.dailyTodos}
              totalFinishedCount={this.finishedTodos.length}
            />
          </li>
        </ul>
        <TodoHistory />
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  todos: state.todos,
  ...ownProps,
});

export default connect(mapStateToProps)(Statistics);
