import React from "react";
import { Button, Input, Modal } from "antd";
import {
  CloseCircleOutlined,
  ExclamationCircleOutlined,
} from "@ant-design/icons";
import axios from "../../config/axios";
import CountDown from "./CountDown";
import "./TomatoAction.scss";

const { confirm } = Modal;

// import CountDown from "./CountDownHooks"; // æ­¤åœºæ™¯ä¸é€‚ç”¨

interface IProps {
  startTomato: () => void;
  unFinishedTomatoes: any;
  updateTomato: any;
}

interface IState {
  description: string;
}

class TomatoAction extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      description: "",
    };
  }

  onKeyUp = (e: any) => {
    if (e.keyCode === 13 && this.state.description !== "") {
      this.addDescription();
    }
  };

  onFinish = () => {
    this.forceUpdate();
  };

  addDescription = () => {
    this.updateTomato({
      description: this.state.description,
      ended_at: new Date(),
    });
    this.setState({
      description: "",
    });
  };

  abortTomato = () => {
    this.updateTomato({ aborted: true });
  };

  updateTomato = async (params: any) => {
    try {
      const response = await axios.put(
        `tomatoes/${this.props.unFinishedTomatoes.id}`,
        params
      );
      this.props.updateTomato(response.data.resource);
    } catch (error) {}
  };

  showConfirm = () => {
    confirm({
      title: "æ‚¨ç›®å‰æ­£åœ¨ä¸€ä¸ªç•ªèŒ„å·¥ä½œæ—¶é—´ä¸­ï¼Œè¦æ”¾å¼ƒè¿™ä¸ªç•ªèŒ„å—ï¼Ÿ",
      icon: <ExclamationCircleOutlined />,
      onOk: () => {
        this.abortTomato();
      },
      onCancel() {},
      okText: "ç¡®å®šæ”¾å¼ƒ",
      cancelText: "å–æ¶ˆ",
    });
  };

  render() {
    let html = <div></div>;

    if (this.props.unFinishedTomatoes === undefined) {
      html = (
        <Button
          className="startTomatoButton"
          onClick={() => this.props.startTomato()}
        >
          å¼€å§‹ç•ªèŒ„
        </Button>
      );
    } else {
      const startedAt = Date.parse(this.props.unFinishedTomatoes.started_at);
      const duration = this.props.unFinishedTomatoes.duration;
      const timeNow = new Date().getTime();
      if (timeNow - startedAt > duration) {
        html = (
          <div className="inputWrapper">
            <Input
              value={this.state.description}
              placeholder="è¯·è¾“å…¥ä½ åˆšåˆšå®Œæˆçš„ä»»åŠ¡"
              onChange={(e) => this.setState({ description: e.target.value })}
              onKeyUp={(e) => this.onKeyUp(e)}
            />
            <CloseCircleOutlined className="abort" onClick={this.showConfirm} />
          </div>
        );
      } else if (timeNow - startedAt < duration) {
        const timer = startedAt + duration - timeNow;
        html = (
          <div className="countDownWrapper">
            <CountDown
              duration={duration}
              timer={timer}
              onFinish={this.onFinish}
            />
            <CloseCircleOutlined className="abort" onClick={this.showConfirm} />
          </div>
        ); // å€’è®¡æ—¶
      }
    }

    return (
      <div className="TomatoAction" id="TomatoAction">
        {html}
      </div>
    );
  }
}

export default TomatoAction;
