import React from "react";
import { connect } from "react-redux";
import { format } from "date-fns";
import { updateTodo } from "../../../redux/actions";
import axios from "../../../config/axios";
import "./TodoHistoryTodoItem.scss";

interface IProps {
  todo: any;
  itemType: string;
  updateTodo: (payload: any) => void;
}

class TodoHistoryTodoItem extends React.Component<IProps> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  updateTodo = async (params: any) => {
    try {
      const response = await axios.put(`todos/${this.props.todo.id}`, params);
      this.props.updateTodo(response.data.resource);
    } catch (error) {}
  };

  render() {
    let action;
    let time = Date.parse(this.props.todo.updated_at);
    let formatText = "HH:mm";
    if (this.props.itemType === "finished") {
      formatText = "HH:mm";
      time = Date.parse(this.props.todo.updated_at);
      action = (
        <div className="action">
          <span
            onClick={() =>
              this.updateTodo({
                completed: false,
              })
            }
          >
            æ¢å¤
          </span>
          <span
            onClick={() =>
              this.updateTodo({
                deleted: true,
              })
            }
          >
            åˆ é™¤
          </span>
        </div>
      );
    } else if (this.props.itemType === "deleted") {
      formatText = "yyyy-MM-dd HH:mm";
      time = Date.parse(this.props.todo.created_at);
      action = (
        <div className="action">
          <span
            onClick={() =>
              this.updateTodo({
                deleted: false,
              })
            }
          >
            æ¢å¤
          </span>
        </div>
      );
    }
    return (
      <div className="TodoHistoryTodoItem" id="TodoHistoryTodoItem">
        <div className="text">
          <span className="time">{format(time, formatText)}</span>
          <span className="description">{this.props.todo.description}</span>
        </div>
        {action}
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => ({
  todos: state.todos,
  ...ownProps,
});

const mapDispatchToProps = { updateTodo };

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TodoHistoryTodoItem);
